@import url(https://fonts.googleapis.com/css?family=Bree+Serif&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;overflow-y:overlay}.App{min-height:100vh;font-size:1rem;background:#222;color:#fff;padding:20px}.logoImg{height:4rem;pointer-events:none;margin-left:10px;margin-right:12px}.logoText{margin:0;font-size:2rem;font-family:"Bree Serif", serif;letter-spacing:0.1rem;color:#eee}header,section,footer{width:100%;max-width:1200px;margin:0 auto}.App-header{display:flex;margin-bottom:10px;margin-top:30px}.App-footer{font-size:0.8em;margin-top:20px;color:#666}

.spaceContainer{max-width:1000px;min-height:600px;margin:40px auto;background:#222 radial-gradient(circle, rgba(17,18,21,0.8) 0%, rgba(32,32,49,0.65098) 30%, #222 60%);border:1px solid rgba(255,255,255,0.1);border-radius:8px;position:relative;overflow:hidden}.spaceContainer .uiBox{display:block;margin:20px auto 0;max-width:400px;color:#fff;background-color:rgba(255,255,255,0.3)}.spaceContainer .uiBox form{max-width:500px;padding:8px;display:flex;flex-flow:row wrap;justify-content:center;align-items:strectch}.spaceContainer .uiBox form>*{margin:0.5rem;flex:3 1 10ch}.spaceContainer .uiBox form button{flex:1 3 10ch}

